body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

#header {
  width: 960px;
  height: 500px;
  margin: 20px auto 20px auto;
  text-align: center;
  position: relative;
  font-family:"Bungee Inline", cursive;
  border-radius: 5px;
}

#left {
  width: 610px;
  height: 500px;
  float: left;
  background-image: url(/static/media/work_hand.c5438238.jpg);
  background-size: 100% auto;
  background-repeat: no-repeat;
}

#right {
  width: 350px;
  height: 500px;
  float: right;
  background-image: url(/static/media/hand_light.10cc7b4a.jpg);
  background-size: 100% auto;
  background-repeat: no-repeat;
}

#title {
  justify-content: center;
  font-size: 100px;
  color: #facf36;
  text-align: center; 
  position: absolute;
  top: 20%;
  left: 3%;
}
.mess {
  width: 960px;
  margin: 5px auto 5px auto;
  background-color: #f8e289;
  border-radius: 5px;
  padding: 5px;
}

img {
  float: right;
}
.map {
    height: 500px;
    width: 600px;
   margin-left: auto;
   margin-right: auto;
}

a {
  margin: 0 20px 0 20px;
}
.wrapper {
  height: 100%;
  width: 960px;
  margin: auto;
  overflow: hidden;
}


.App {
  text-align: center;

}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

body {
  background-image: url(/static/media/congruent_pentagon.c0864a9f.png);
  background-size: 100%;
}

